@font-face {
  font-family: 'Open Sans Bold';
  src: url('../../fonts/OpenSans-Bold.eot');
  src: url('../../fonts/OpenSans-Bold.eot?#iefix') format('embedded-opentype'), url('../../fonts/OpenSans-Bold.woff') format('woff'), url('../../fonts/OpenSans-Bold.ttf') format('truetype');
  font-weight: bold;
  font-style: normal;
}
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  outline: none;
}
html {
  font-size: 10px;
}
body {
  font-size: 16px;
}
.has-error input {
  background-color: #e72c2c71 !important;
}
.has-success input {
  background-color: #4bb54354 !important;
}
.container {
  max-width: 1170px;
  width: 100%;
  margin: auto;
}
.header {
  padding: 1rem 0 3.5rem;
  width: 100%;
  background: #ffffff;
  position: fixed;
  top: 0;
  box-shadow: 2px 2px 7px rgba(0, 0, 0, 0.15);
  z-index: 10;
}
.header .header-wrapp {
  display: flex;
  flex-direction: column;
}
.header .logo {
  margin: 0 auto -4rem;
  position: relative;
  left: 2.5rem;
}
.header .nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}
.header .nav ul {
  display: flex;
  flex-direction: row;
}
.header .nav ul li {
  margin: 0 3rem;
  list-style: none;
}
.header .nav ul li a {
  font-weight: 400;
  text-decoration: none;
  position: relative;
  font-family: 'Open Sans Semibold';
  color: #555555;
  font-size: 0.875em;
}
.header .nav ul li a:hover {
  color: #5363db;
}
.header .nav ul li a:hover:before {
  content: '';
  position: absolute;
  bottom: -0.7rem;
  right: 50%;
  margin-right: -1.5rem;
  width: 3rem;
  height: 0.1rem;
  background: #5363db;
}
.slider {
  background: linear-gradient(rgba(83, 99, 219, 0.8)), url('../../build/img/bg-slider.png') center center no-repeat;
  background-size: cover;
  display: flex;
  padding-top: 8rem;
}
.slider .slider-wrapp {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8rem 0;
}
.slider .slider-wrapp .slider-form {
  margin: 0 2rem;
}
.slider .slider-wrapp .slider-form .form-items {
  width: 300px;
  padding: 3rem 3rem 2.5rem 3rem;
  border-radius: 1.5rem;
  display: flex;
  flex-direction: column;
  background: #ffffff;
}
.slider .slider-wrapp .slider-form .form-items h2 {
  font-size: 1.625em;
  margin-bottom: 3.1rem;
  color: #5363db;
  font-family: 'Open Sans Semibold';
  font-weight: 400;
}
.slider .slider-wrapp .slider-form .form-items .form-item:nth-child(-n+2) {
  margin-bottom: 17px;
}
.slider .slider-wrapp .slider-form .form-items .form-item:nth-child(3) {
  margin-bottom: 32px;
}
.slider .slider-wrapp .slider-form .form-items .form-item {
  position: relative;
  display: flex;
  justify-content: flex-end;
}
.slider .slider-wrapp .slider-form .form-items .form-item input,
.slider .slider-wrapp .slider-form .form-items .form-item select {
  border: 1px solid transparent;
  width: 100%;
  height: 50px;
  background: #f2f2f2;
  color: #888;
  padding-left: 10px;
  border-radius: 6px;
}
.slider .slider-wrapp .slider-form .form-items .form-item select {
  border: 0;
  -webkit-appearance: none;
}
.slider .slider-wrapp .slider-form .form-items .form-item select option {
  background: #f2f2f2;
}
.slider .slider-wrapp .slider-form .form-items .form-item ::-webkit-input-placeholder,
.slider .slider-wrapp .slider-form .form-items .form-item ::-moz-placeholder,
.slider .slider-wrapp .slider-form .form-items .form-item :-ms-input-placeholder {
  color: #888888;
}
.slider .slider-wrapp .slider-form .form-items .form-item .btn-form {
  width: 170px;
  height: 50px;
  background: #5b6ceb;
  border: 0;
  border-radius: 8px;
  align-self: flex-end;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  color: #ffffff;
  font-size: 1em;
  font-family: 'Open Sans';
  font-weight: 700;
}
.slider .slider-wrapp .slider-form .form-items .form-item .btn-form:hover {
  background: #374cee;
}
.slider .slider-wrapp .slider-form .form-items .form-item:nth-child(3) {
  border-radius: 6px;
  background: #f2f2f2;
}
.slider .slider-wrapp .slider-form .form-items .form-item:nth-child(3):after {
  display: block;
  content: "";
  height: 13px;
  width: 14px;
  background: url('../../build/img/arrow-down.png') no-repeat center;
  position: absolute;
  top: 20px;
  right: 15px;
}
.slider .slider-wrapp .slider-text {
  display: flex;
  margin-left: 9.1rem;
  flex-direction: column;
}
.slider .slider-wrapp .slider-text h1 {
  font-size: 3em;
  font-weight: 700;
  line-height: 72px;
  text-transform: uppercase;
  margin-bottom: 2.7rem;
  color: #ffffff;
  font-family: 'Open Sans Bold';
}
.slider .slider-wrapp .slider-text p {
  color: #ffffff;
  font-size: 2.5em;
  line-height: 45px;
  margin-bottom: 0;
  font-family: 'FreestyleScript';
}
.blog .blog-title {
  text-align: center;
  padding: 125px 0 76px;
}
.blog .blog-title h2 {
  margin-bottom: 25px;
  line-height: 45px;
  text-transform: uppercase;
  font-size: 1.875em;
  color: #5b6ceb;
  font-family: "Open Sans Bold";
}
.blog .blog-title p {
  color: #777777;
  font-family: 'FreestyleScript';
  font-size: 2.56em;
  font-weight: 400;
  line-height: 61.49px;
  margin-bottom: 0;
}
.blog .blog-program {
  padding-bottom: 12.5rem;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}
.blog .article {
  width: 48.7%;
  display: inherit;
  flex-direction: inherit;
  margin-bottom: 2.5rem;
  box-shadow: 0.4rem 0.4rem 2rem rgba(0, 0, 0, 0.1);
  border-radius: 8px;
}
.blog .article:nth-last-of-type(-n+2) {
  margin-bottom: 0;
}
.blog .article .blog-program--img {
  width: 270px;
  height: 300px;
  border-radius: 5px 0 0 5px;
}
.blog .article .blog-program--text {
  padding: 5rem 3rem 0 3rem;
  width: 300px;
  border-radius: 0 5px 5px 0;
}
.blog .article .blog-program--text h3 {
  color: #777;
  margin-bottom: 3.6rem;
  font-family: "Open Sans Bold";
  font-size: 1.25em;
  font-weight: 700;
  line-height: 45px;
  text-transform: uppercase;
}
.blog .article .blog-program--text h3 a {
  color: inherit;
}
.blog .article .blog-program--text p {
  margin-bottom: 2rem;
  color: #555555;
  font-family: "Open Sans Regular";
  font-size: 0.875em;
  font-weight: 400;
  line-height: 22px;
}
.blog .article .blog-program--text span {
  color: #999999;
  font-family: "Open Sans Regular";
  font-size: 0.875em;
  font-weight: 400;
  line-height: 22px;
}
.gallery ul {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}
.gallery li {
  width: 450px;
  position: relative;
  list-style: none;
}
.gallery li img {
  width: 100%;
  height: auto;
}
.gallery li a {
  width: 100%;
  height: 100%;
  display: none;
  justify-content: center;
  align-items: center;
  background: linear-gradient(rgba(91, 108, 235, 0.8)), url('') center center no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
}
.gallery li a:after {
  content: '';
  width: 57px;
  height: 57px;
  border-radius: 50%;
  display: block;
  background: url(../../build/img/icon-search.png) center center no-repeat;
  background-color: #ffffff;
}
.gallery li:hover a {
  display: flex;
}

@media screen and (max-width: 1200px) {
  .blog .article {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .blog .article img {
    border-radius: 5px;
  }
  .blog .article .blog-program--text {
    box-shadow: none;
    padding: 3rem 0 2rem 3rem;
  }
}
@media screen and (max-width: 992px) {
  .header .nav ul li {
    margin: 0 2.5rem;
  }
  .slider .slider-wrapp .slider-text {
    margin-left: 3.5rem;
  }
  .slider .slider-wrapp .slider-text h1 {
    font-size: 2.5em;
  }
  .slider .slider-wrapp .slider-text p {
    font-size: 2em;
  }
  .gallery li {
    margin: 0 2rem 1rem;
    display: flex;
  }
}
@media screen and (max-width: 900px) {
  .header .nav ul li {
    margin: 0 1.5rem;
  }
  .slider .slider-wrapp {
    flex-direction: column-reverse;
    padding: 4rem 0;
  }
  .slider .slider-wrapp .slider-text {
    padding-bottom: 4rem;
  }
  .slider .slider-wrapp .slider-text h1,
  .slider .slider-wrapp .slider-text p {
    text-align: center;
  }
}
@media screen and (max-width: 768px) {
  body .header .header-wrapp .menu-mobile {
    display: block;
    margin-left: 2rem;
    background: transparent;
    border: none;
  }
  body .header .header-wrapp .nav {
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
  }
  body .header .header-wrapp .nav ul {
    display: none;
    text-align: center;
    margin: 0;
  }
  body .header .header-wrapp .nav ul:nth-of-type(1) {
    padding-top: 3.5rem;
  }
  body .header .header-wrapp .nav li {
    text-align: left;
    margin: 0 2rem 2rem;
  }
  body .blog .article .blog-program--text {
    width: 260px;
    padding: 5rem 1rem 0;
  }
  body .blog .blog-title {
    padding: 3rem 0;
  }
  body .blog .blog-program {
    padding-bottom: 3rem;
  }
}
@media screen and (max-width: 650px) {
  .blog .blog-program {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .blog .blog-program .article {
    margin-bottom: 2rem;
  }
  .blog .blog-program .article .blog-program--text {
    padding: 2rem 0.5rem 3.5rem 1.5rem;
  }
  .blog .blog-program .article:last-child {
    margin: 0;
  }
}
@media screen and (max-width: 500px) {
  .slider .slider-wrapp .slider-form .form-items {
    width: 255px;
    padding: 1.5rem 1.5rem 1rem;
  }
  .slider .slider-wrapp .slider-text {
    margin-left: 0;
  }
  .slider .slider-wrapp .slider-text h1 {
    font-size: 1.8em;
    line-height: 56px;
  }
  .slider .slider-wrapp .slider-text p {
    font-size: 1.7em;
  }
}
@media screen and (max-width: 520px) {
  .blog .article {
    min-width: 84.5%;
  }
}
